.movie-card {
  z-index: 1;
  position: relative;
  --description-width: 0;
  --description-boder-right-width: 0;
  --description-boder-width: 0;
  cursor: pointer;

  width: 200px;
  height: 300px;
  background-color: #000;
  transition: all 0.2s ease;

  &__img {
    position: relative;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__description {
    background-color: #fff;
    overflow: hidden;
    position: absolute;
    box-sizing: border-box;
    transition: width 1s cubic-bezier(0,1.09,.2,.98), border-right 1s cubic-bezier(1,-0.06,.42,.95);
    border: var(--description-boder-width) solid $c-gray;
    border-right: var(--description-boder-right-width) solid $c-red;
    width: var(--description-width);
    left: 100%;
    right: 0;
    top: 0;
    bottom: 0;
  }

  &__description-img {
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__description-content {
    margin-left: 10px;
  }

  &__description-title {
    z-index: 3;
    display: block;
    width: 300px;
    height: max-content;
  }

  &__description-plot {
    display: block;
    width: 200px;
    height: 100%;
    font-size: 12px;
    color: $c-gray;
    font-weight: 200;
  }

  &:hover {
    --description-boder-width: 1px;
    --description-width: 300px;
    --description-boder-right-width: 60px;
    // transform: translateY(-4px);
    // --description-display: block; 
  }
}